import React, { useState, useEffect } from 'react';
import axios from 'axios';
import ConfirmationModal from './ConfirmationModal';
import AdmissionModal from './AdmissionModal';

const API_BASE_URL = 'http://localhost:3000/api/v1';

const BedCard = ({ bedData, onStatusChange, token, userRole }) => {
    const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
    const [isAdmissionModalOpen, setIsAdmissionModalOpen] = useState(false);
    const [isDischargeModalOpen, setIsDischargeModalOpen] = useState(false);
    const [status, setStatus] = useState(bedData.current_status);
    const bedId = bedData.bed_id;

    useEffect(() => {
        setStatus(bedData.current_status);
    }, [bedData.current_status]);

    // --- API HANDLERS ---
    
    const handleDelete = async () => {
        try {
            await axios.delete(`${API_BASE_URL}/beds/${bedId}`, {
                headers: { Authorization: `Bearer ${token}` }
            });
            onStatusChange(); 
        } catch (err) {
            alert("Delete failed. Ensure the bed is empty and clean before removing.");
        } finally {
            setIsDeleteModalOpen(false);
        }
    };

    const handleAdmission = async (patientName) => {
        try {
            await axios.post(`${API_BASE_URL}/beds/${bedId}/status`, 
                { new_status: 'OCCUPIED', patient_name: patientName },
                { headers: { 'Authorization': `Bearer ${token}` } }
            );
            onStatusChange();
            setIsAdmissionModalOpen(false);
        } catch (error) {
            alert('Admission failed.');
        }
    };

    const handleDischarge = async () => {
        try {
            await axios.post(`${API_BASE_URL}/beds/${bedId}/status`, 
                { new_status: 'CLEANING' },
                { headers: { 'Authorization': `Bearer ${token}` } }
            );
            onStatusChange();
            setIsDischargeModalOpen(false);
        } catch (error) {
            alert('Discharge failed.');
        }
    };

    const handleMarkClean = async () => {
        try {
            await axios.post(`${API_BASE_URL}/beds/${bedId}/status`, 
                { new_status: 'AVAILABLE' },
                { headers: { 'Authorization': `Bearer ${token}` } }
            );
            onStatusChange();
        } catch (error) {
            alert('Status update failed.');
        }
    };

    // --- UI HELPERS ---

    const isLongStay = () => {
        if (status !== 'OCCUPIED' || !bedData.updated_at) return false;
        const lastUpdate = new Date(bedData.updated_at);
        const now = new Date();
        const diffInHours = (now - lastUpdate) / (1000 * 60 * 60);
        return diffInHours >= 48; 
    };

    const getCardStyle = () => {
        let base = "";
        switch (status) {
            case 'AVAILABLE': base = 'bg-green-50 text-green-800 border-green-200'; break;
            case 'OCCUPIED': base = 'bg-red-50 text-red-800 border-red-200'; break;
            case 'CLEANING': base = 'bg-yellow-50 text-yellow-800 border-yellow-200'; break;
            default: base = 'bg-gray-50 text-gray-800 border-gray-200';
        }
        if (bedData.specialty_type === 'Maternity') base += ' border-l-8 border-l-pink-400';
        if (bedData.specialty_type === 'ICU') base += ' border-l-8 border-l-indigo-600';
        if (isLongStay()) base += ' ring-2 ring-red-500 ring-offset-2';
        return base;
    };

    return (
        <div className={`p-5 border-2 rounded-xl shadow-sm transition-all flex flex-col justify-between relative min-h-[240px] ${getCardStyle()}`}>

            {/* Long Stay Badge */}
            {isLongStay() && (
                <div className="absolute -top-3 left-4 bg-red-600 text-white text-[10px] font-bold px-2 py-1 rounded shadow-lg animate-bounce z-20">
                    ‚ö†Ô∏è LONG STAY {'> 48 hrs'}
                </div>
            )}

            {/* Delete Button */}
            {userRole?.toUpperCase() === 'ADMIN' && (
                <button 
                    onClick={() => setIsDeleteModalOpen(true)}
                    className="absolute top-3 right-3 p-2 bg-white bg-opacity-60 hover:bg-red-500 hover:text-white text-red-600 rounded-full transition-all z-30 shadow-sm border border-red-100"
                    title="Remove Bed"
                >
                    <span className="text-sm">üóëÔ∏è</span>
                </button>
            )}

            {/* MODALS */}
            <AdmissionModal 
                isOpen={isAdmissionModalOpen}
                onClose={() => setIsAdmissionModalOpen(false)}
                onConfirm={handleAdmission}
                bedNumber={bedData.bed_number}
            />

            <ConfirmationModal
                isOpen={isDischargeModalOpen}
                onClose={() => setIsDischargeModalOpen(false)}
                onConfirm={handleDischarge}
                title="Confirm Discharge"
                message={`Are you sure you want to discharge ${bedData.patient_name || 'the patient'}? The bed will be set to CLEANING status.`}
            />

            <ConfirmationModal
                isOpen={isDeleteModalOpen}
                onClose={() => setIsDeleteModalOpen(false)}
                onConfirm={handleDelete}
                title="Delete Bed"
                message={`This will permanently remove Bed ${bedData.bed_number} from ${bedData.ward_name}.`}
            />

            {/* CARD CONTENT */}
            <div>
                <div className="flex justify-between items-start pr-8">
                    <h3 className="font-extrabold text-xl text-slate-900 tracking-tight">
                        Bed {bedData.bed_number || bedId}
                    </h3>
                    <span className="text-[10px] uppercase font-bold px-2 py-0.5 rounded-full border border-current">
                        {status}
                    </span>
                </div>
                
                <div className="mt-2 text-sm space-y-1">
                    <p><span className="font-semibold text-slate-500">Ward:</span> {bedData.ward_name}</p>
                    <p><span className="font-semibold text-slate-500">Specialty:</span> {bedData.specialty_type}</p>
                </div>

                {status === 'OCCUPIED' && (
                    <div className="mt-4 p-3 bg-white bg-opacity-80 rounded-lg border border-red-100 shadow-sm">
                        <p className="text-[9px] uppercase font-bold text-red-500 mb-0.5">Current Patient</p>
                        <p className="text-base font-bold text-red-900 truncate">
                            üë§ {bedData.patient_name || 'Anonymous'}
                        </p>
                        {isLongStay() && (
                            <p className="text-[10px] text-red-600 mt-1 font-medium italic animate-pulse">
                                * Review required: Stay exceeds 48h
                            </p>
                        )}
                    </div>
                )}
            </div>
            
            {/* ACTION BUTTONS */}
            <div className="mt-5 flex flex-col gap-2">
                {status === 'OCCUPIED' && (
                    <button className="bg-red-600 text-white font-bold py-2.5 rounded-lg text-xs hover:bg-red-700 transition-colors shadow-md"
                        onClick={() => setIsDischargeModalOpen(true)}>Discharge Patient</button>
                )}
                {status === 'CLEANING' && (
                    <button className="bg-yellow-500 text-white font-bold py-2.5 rounded-lg text-xs hover:bg-yellow-600 transition-colors shadow-md"
                        onClick={handleMarkClean}>Mark Clean</button>
                )}
                {status === 'AVAILABLE' && (
                    <button className="bg-green-600 text-white font-bold py-2.5 rounded-lg text-xs hover:bg-green-700 transition-colors shadow-md"
                        onClick={() => setIsAdmissionModalOpen(true)}>Direct Admission</button>
                )}
            </div>
        </div>
    );
};

export default BedCard;